.navbar-mark{
    width: 100%;
    height: 114px;
    display: flex;
    flex-direction: row;
    justify-content: center;
    background-color: white;
    align-items: center;
    border-right: 1px solid rgba(128, 128, 128, 0.151);
}
.navbar{
    width: 255px;
}
.navbar svg{
    font-size: 23px !important;
}
.navbar-mark img:nth-child(1){
    margin-right: 10px;
    width: 30px;
}

.navbar-mark img:nth-child(2){
   width: 120px;
}

.header{
    height: 65px;
    width: 100%;
    display: flex;
    flex-direction: row;
    align-items: center;
    padding: 0px 20px;
    box-sizing: border-box;
}

.landing-contain{
    display: flex;
    flex-direction: row;
}

.landing-contain-content{
    width: calc(100% - 255px);
    height: 100%;
}
.landing-contain-section{
    width: 100%;
    height: 100%;
    padding: 0px 14px;
    box-sizing: border-box;
}

.live-ended{
    width: 200px;
    border: 1px solid #f0f6ff;
    /* margin-bottom: 33px; */
    border-radius: 15px;
    margin-top: 24px;
}

.live-ended button{
    height: 34px;
    width: 100px;
    border-radius: 22px;;
    border: none;
    outline: none;
    background-color: white;
    cursor: pointer;
    font-weight: bold;
}

.live-ended .selected{
    color: white;
    background-color: #0f7efe;
}
.tasks-button{
    display: flex;
    width: 371px;
    justify-content: space-around;
    padding: 5px 0px;
    border: 1px solid #f0f6ff;
    border-radius: 10px;
}
.tasks-button button{
    font-weight: bold;
    height: 34px;
    width: 30%;
    border-radius: 5px;
    border: none;
    background-color: white;
    cursor: pointer;
}
.tasks-button .selected{
    background-color: #e2e7f8;
    color: #0f7efe;
}
.landing-contain-section .ant-card-body{
    padding-top: 5px!important;
    padding-bottom: 10px!important;
}
.admin-avartar{
    display: flex;
    flex-direction: row;
    justify-content: space-around;
    align-items: center;
}

.admin-avartar img{
    width: 51px;
    background-color: #3654d6;
    border-radius: 32px;
    margin-right: 3px;
}

.admin-avartar span{
    color: gray;
}

.navbar .ant-card-body{
    padding-left: 5px !important;
    padding-right: 5px !important;
    width: 204px;
    display: flex;
    flex-direction: column;
    justify-content: center;
}

.navbar .ant-card-body h5{
    margin: 3px!important;
}
.admin-avartar{
    margin-bottom: 28px;
}
.task-table img{
    width: 48px;
    margin-left: 50%;
    transform: translate(-50%, 0);
}

.task-table{
    width: 100%;
    border-collapse: collapse;
    margin-top: 24px;
}

.task-table h4{
    margin-top: 2px;    
    margin-bottom: 1px;
}

.task-table p{
    margin-top: 1px;
}
.task-table tr{
    height: 100px;
    /* border-bottom: 1px solid gray !important; */
}

.task-table th, td{
   border-bottom: 1px solid #f3f3f3;
}

.task-table th{
    text-align: left;
}