.login-page{
    position: absolute;
    top: 30%;
    left: 50%;
    transform: translate(-50%, -30%);
    width: 400px;
    display: flex;
    flex-direction: column;
    align-items: center;
    padding-top: 25px 40px;
}

.login-page img{
    width: 280px;
    margin-bottom: 50px;
}

.login-page h1{
    margin: 0px;
}

.login-page p{
    color: gray;
    padding-bottom: 24px;
    border-bottom: 1px solid #f2f2f4;
    margin-bottom: 40px;
}

.forgot-password{
    color: #3350ce;
    font-weight: bold;
    width: 100%;
    height: 20px;
    margin-top: 30px;
    display: flex;
    justify-content: center;
    align-items: center;
}


 
