.delete-modal-footer{
    margin-top: 40px;
    width: 70%;
    display: flex;
    margin-left: 15%;
    justify-content: space-between;
}

.delete-modal-body p{
    color: red;
    margin: 0px;
}
.delete-modal-body {
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
    font-weight: bold;
}

.delete-modal-body img{
    width: 130px;
    margin-bottom: 20px;
}
.delete-modal-header{
    display: flex;
    flex-direction: row;
}
.delete-modal-header div{
    width: 95%;
    color: red;
    display: flex;
    font-weight: bold;
    justify-content: center;
    margin-left: 35px;
}
.delete-modal-header h1{
    font-size: 20px;
}

.delete-modal-body-img{
    background-image: url("../../assets/bin.png");
    width: 100px;
    height: 100px;
    background-size: 100% 100%;
}